import React, { useState, useEffect } from "react";
import Item from "./Item";
import "../App.css";
import { IoIosArrowDropdownCircle } from "react-icons/io";

function Type(props) {
  const [display, setDisplay] = useState(false);
  const [items, setItems] = useState([]);

  const toggle = () => {
    setDisplay(!display);
    console.log(display);
  };

  // useEffect(() => {
  //   console.log(items);
  // }, [items]);

  useEffect(() => {
    if (props.info === undefined) return;
    var list = [];
    for (var i = 0, max = props.info.length; i < max; i++) {
      // console.log(props.info[i]);
      var arr = [
        props.info[i]["name"],
        props.info[i]["spicy"],
        props.info[i]["veg"],
        props.info[i]["price"],
      ];
      // list.push(arr);
      list.push(<Item key={arr[0]} info={arr} />);
      // console.log(arr);

      // out = <Item data={sort[types[i]]} />;
    }
    setItems(list);
  }, [props.info]);

  const Content = () => {
    // var rows = [];
    // for (var i = 0; i < items.length; i++) {
    //   rows.push(<Item key={items[i][0]} info={items[i]} />);
    // }
    if (!display) return null;
    return <div className="bg-gray-200 bg-opacity-50">{items}</div>;
  };

  return (
    <div className="flex flex-col mx-2 my-2">
      <div className="flex flex-row type p-1 font-semibold" onClick={toggle}>
        {props.type}
        {!display && (
          <IoIosArrowDropdownCircle className="ml-auto my-auto text-xl" />
        )}
        {display && (
          <IoIosArrowDropdownCircle
            className="ml-auto my-auto text-xl"
            style={{ transform: "rotate(180deg)" }}
          />
        )}
      </div>
      <Content />
    </div>
  );
}

export default Type;
